import { edgesByName } from '../utils/gql';

export const pageQuery = graphql`
  {
    images: allFile(filter: { relativeDirectory: { eq: "grow-sales" } }) {
      edges {
        node {
          name
          childImageSharp {
            fluid {
              base64
              tracedSVG
              srcWebp
              srcSetWebp
              originalImg
              originalName
            }
          }
        }
      }
    }
    banner: file(relativePath: { eq: "banners/why-sell-banner.png" }) {
      childImageSharp {
        fluid {
          base64
          tracedSVG
          srcWebp
          srcSetWebp
          originalImg
          originalName
        }
      }
    }
    companies: allFile(filter: { relativeDirectory: { eq: "companies" } }) {
      edges {
        node {
          name
          childImageSharp {
            fluid {
              base64
              tracedSVG
              srcWebp
              srcSetWebp
              originalImg
              originalName
            }
          }
        }
      }
    }
  }
`;

